.cart-product-list-item-description {
    margin: 25px 0;
    padding: 15px;
    border: 1px dotted;
    border-radius: 10px;
    color: black;
}
.cart-product-list-item-description img {
    display: block;
    margin: 0 auto;
}
.cart-extended-name {
    font-size: 20px;
    text-align: center;
    margin-bottom: 15px;
}
.cart-extended-price {
 font-size: 13px;
 font-style: italic;
 margin-bottom: 10px;
}
.bold {
    font-weight: bold;
}
.cart-extended-sum {
    font-size: 16px;
    text-align: right;
}
.sum-price {
    font-size: 20px;
    color: #3366cc;
}
.cart-product-list-item-description {
    background: white;
}
.row {
    display: flex;
}
.col-lg-3 {
    flex:0 0 25%;
    max-width: 25%;
}
.col-lg-9 {
    flex: 0 0 75%;
    max-width: 75%;
}
.cart-like-button {
    display: flex;
    justify-content: flex-end;
}
.cart-like-btn {
    width: 25px;
    height: 25px;
}
